<template>
  <div class="container-fluid">
    <div class="row">
      <div class="col-md-12">
        <div class="card">
          <div class="card-header card-header-primary card-header-icon">
            <div class="card-icon">
              <i class="material-icons">remove_red_eye</i>
            </div>
            <h4 class="card-title">
                View
              <strong>Routes</strong>
            </h4>
          </div>
          <div class="card-body">
            <back-button></back-button>
          </div>
          <div class="card-body">
            <div class="row">
              <div class="col-md-12">
                <div class="table-responsive">
                  <div class="table">
                    <tbody>
                      <tr>
                        <td class="text-primary">
                            Origin
                        </td>
                        <td>
                            {{entry.origin }}
                        </td>
                      </tr>
                       <tr>
                        <td class="text-primary">
                            Destination
                        </td>
                        <td>
                            {{entry.destination }}
                        </td>
                      </tr>
                      <tr>
                        <td class="text-primary">
                            Waypoints
                        </td>
                        <td>
                          <datatable-list
                            :row="entry"
                            field="pitstops"
                          >
                          </datatable-list>
                        </td>
                      </tr>
                    </tbody>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import DatatableList from '@components/Datatables/DatatableList'

export default {
  components: {
    DatatableList
  },
  data() {
    return {}
  },
  beforeDestroy() {
    this.resetState()
  },
  computed: {
    ...mapGetters('RoutesSingle', ['entry'])
  },
  watch: {
    '$route.params.id': {
      immediate: true,
      handler() {
        this.resetState()
        this.fetchShowData(this.$route.params.id)
      }
    }
  },
  methods: {
    ...mapActions('RoutesSingle', ['fetchShowData', 'resetState'])
  }
}
</script>
